import React, {useState} from "react";

//    :  ,    
export const useCamera = (
    setPhotoUrl: any, //    URL  (  )
    setError: any, //    
    videoRef: React.RefObject<HTMLVideoElement>, //    video,      
    canvasRef: React.RefObject<HTMLCanvasElement>, //    canvas,      
    streamRef: any //      (  )
) => {
    const [isCameraOn, setIsCameraOn] = useState(false);// ,    

    //    
    const startCamera = async () => {
        const startVideo = async () => {
            try {
                //       API getUserMedia
                const stream = await navigator.mediaDevices.getUserMedia({
                    video: {facingMode: "user"}, //      ()
                });
                streamRef.current = stream; //     (  )
                if (videoRef.current) videoRef.current.srcObject = stream;//     video
                setIsCameraOn(true);
                setPhotoUrl(null); //  URL  (  ,    )
                setError(null); //  ,   
            } catch (err) {
                //       ,  
                setError("   ");
                console.error(err);
            }
        }
        startVideo()
    };

    //    
    const stopCamera = () => {

        takePhoto()//     
        if (streamRef.current) {

            //    (  ),   
            streamRef.current.getTracks().forEach((track: { stop: () => any; }) => track.stop());
        }
        setIsCameraOn(false);
    };

    //      
    const takePhoto = () => {
        if (canvasRef.current && videoRef.current) {
            const canvas = canvasRef.current; //    
            const context = canvas.getContext("2d"); //    
            if (context) {
                //   ,    
                canvas.width = videoRef.current.videoWidth;
                canvas.height = videoRef.current.videoHeight;
                //      
                context.drawImage(videoRef.current, 0, 0, canvas.width, canvas.height);
                //    DataURL   JPEG
                setPhotoUrl(canvas.toDataURL("image/jpeg"));
                return //   ,    
            }
        }
        setPhotoUrl(null) //     ,  URL
    };

    return {
        isCameraOn,
        startCamera,
        stopCamera,
    };
};
