import React, {useState} from "react";
import './Camera.css'
import {Section} from "../../shared/ui/Section/Section";

interface CameraProps {
    videoRef: React.RefObject<HTMLVideoElement>; //    video,      
    canvasRef: React.RefObject<HTMLCanvasElement>; //    canvas,      
    isShow:Boolean, //  ,    
    isFaceDetected:Boolean,    // ,      
}

export const Camera: React.FC<CameraProps> = ({ videoRef, isShow,isFaceDetected,canvasRef }) => {
    const [isLoaded, setIsLoaded] = useState(false);

    const handleVideoLoaded = () => {
        setTimeout(()=>{
            setIsLoaded(true);
        },0)

    };

    return (
        <Section className={`video_box ${isShow && isLoaded?'':'hide'}`}>

            <video
                className="video_online"
                ref={videoRef}
                playsInline
                autoPlay
                muted
                width={'200px'}
                height={'200px'}
                controls={false}
                onLoadedMetadata={handleVideoLoaded}
            />
            <canvas
                ref={canvasRef}
                className={'canvas'}
            />
            {/*        */}
            <div
                className={`face_overlay ${isFaceDetected?'ok':'err'}`}

            />
        </Section>
    );
};
